
























































import commonList from "../../components/common/common-list.vue";
import loadMore from "../../components/common/load-more.vue";

var page;
export default {
	components:{
		
		commonList,
		loadMore,
		show:false
	},
	data() {
		return {
			topicInfo:{
				titlepic:"../../static/demo/topicpic/13.jpeg",
				title:"忆往事，敬余生",
				desc:"我是描述",
				totalnum:1000,
				todaynum:1000,
			},
			sta:'',
			tabIndex:0,
			tabBars:[
				{ name:"分享",id:"moren" },
				{ name:"消息",id:"zuixin" },
			],
			swiperheight:'fixed',
			tablist:[
				{
					loadtext:"上拉加载更多",
					list:[
						
					]
				}
			],
			list:[],
			show:false,
			con:''
		};
	},
	onLoad:function(e){
		console.log(e);
		//判断是否登录
		// var loginRes = this.checkLogin('../dongtai/dongtai', '2');
		// if(!loginRes){return false;}
		// uni.setNavigationBarTitle({
		// 	title: e.con
		// });
		  this.getinfo(e.con);
	},
	
	methods:{
		
		//获取数据
		reg:function(e){
			
				uni.navigateTo({
					url: '../orderInfo/orderInfo?id='+e
				});
			
			
			
		},
		
		getinfo:function(e){
			var _this=this;
			uni.showLoading({title:"加载中..."});
			
			
			uni.request({
				url: _this.apiServer + '/api/v1.PositionManagement/search',
				method: 'POST',
				header: {'content-type' : "application/x-www-form-urlencoded"},
				data:{
					'searchValue':e,
					accessToken:uni.getStorageSync('utoken')
					
				},
				success:function(res){
					uni.hideLoading();
					
					if(res.data.errorCode == '0'){
						console.log(res.data);
						var data=res.data.data.list;
						
						if(data.length>0){
							_this.show=true;
						}else{
							_this.show=false;
						}
						for (var i=0;i<data.length;i++) {
							
							
							_this.list.push(data[i]);	
						}
						
						// if(data.length<10){
						// 	_this.tablist[_this.tabIndex].loadtext="已加载全部"; 
						// }else{
						// 	_this.tablist[_this.tabIndex].loadtext="上拉加载更多"; 
						// }
						
						
						
					}else{
						uni.showToast({title:res.data.data, icon:"none"});
					}
				}
			});
			
			
			
		},
		go:function(){
			if(this.con < 1){uni.showToast({title:'搜索内容不能为空', icon:"none"}); return ;}
			
			uni.navigateTo({
				url: 'seach?con='+this.con
			});
		},
		// 上拉刷新
		getdata(){
			
				
			
			var _this=this;
			console.log(_this.sta);
			uni.showLoading({title:"加载中..."});
			
			
			uni.request({
				url: _this.apiServer + 'app/dtlist',
				method: 'POST',
				header: {'content-type' : "application/x-www-form-urlencoded"},
				data:{
					'sta':_this.sta,
					'page':1
				},
				success:function(res){
					// 关闭下拉刷新
					uni.stopPullDownRefresh();
					if(res.data.status == 'ok'){
						
						
						var data=res.data.data;
						uni.showToast({title:"有"+data.length+"条新职位", icon:"none"});
						console.log(data);
						if(data.length>0){
							for (var i=0;i<data.length;i++) {
								_this.sta=data[0].id;
								
								_this.tablist[_this.tabIndex].list.unshift(data[i]);	
							}
						}else{
							uni.showToast({title:"暂无新动态", icon:"none"});
						}
						
						
						
						
						_this.tablist[_this.tabIndex].loadtext="上拉加载更多";
						console.log(_this.tablist)
						
					}else{
						uni.showToast({title:res.data.data, icon:"none"});
					}
					uni.hideLoading();
				}
			});
			
			
			
		},
		// 上拉加载
		loadmore(){
			console.log('chudil');
			if(this.tablist[this.tabIndex].loadtext!="上拉加载更多"){ return; }
			// 修改状态
			this.tablist[this.tabIndex].loadtext="加载中...";

			var _self=this;
			console.log(page);
			 uni.showNavigationBarLoading();
							
							uni.request({
								url: _self.apiServer + 'app/dtlist',
								data: {'page':page},
								method:"POST",
								header : {'content-type':'application/x-www-form-urlencoded'},
								success: function (res) {
								// _self.product =res.data;
							var res=res.data;
								_self.tablist[_self.tabIndex].loadtext = '';
								console.log(res.data);
								 if(res.data.length == 0||res.data.length=='undefined'){
									 uni.showToast({title:"已经到底了", icon:"none"});
									uni.hideNavigationBarLoading();
									_self.tablist[_self.tabIndex].loadtext= '已加载全部';
									return false;
								 }
								 page++;
								
								 //concat 将两个数组拼接起来
								 // 获取数据
								 setTimeout(()=> {
									 // _this.sta=res.data[0].id;
									 _self.tablist[_self.tabIndex].list= _self.tablist[_self.tabIndex].list.concat(res.data);
									_self.tablist[_self.tabIndex].loadtext = '上拉加载更多';
									uni.hideNavigationBarLoading();
									
			
								 }, 1000);
								 
								
								}
								});
		},
		addx:function(){
			uni.navigateTo({
				url: 'dongtaiadd'
			});
		},
		// tabbar点击事件
		tabtap(index){
			this.tabIndex=index;
		},
	}
}
